.tool-container {
    display: flex;
    background-color: #F7F7F7;
    flex-direction: column;
    align-items: center;
    width: 120px;
    border-radius: 16px;
    justify-content: space-between;

}

.tool-icon {
    padding-top: 20px;
}

.tool-title {
    font-weight: 600;
    padding: 15px 0px;
}

@media only screen and (min-width: 400px) {
    .tool-container {
        width: 150px;
    }
}
.home-container {
  padding: 0 30px;
  margin: 0 auto;
  max-width: 1100px;
  display: flex;
  flex-direction: column;
  flex-basis: auto;
  align-items: flex-start;
  background-color: #f7f7f7;
}

.home-underline {
  text-decoration: underline;
  text-underline-offset: 10px;
}

/* Title*/
.home-title-container {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-around;
  width: 100%;
}

.home-title-text {
  font-size: 5rem;
  color: #ff5925;
  margin: 0 0;
  text-align: start;
  flex: 1 0;
}

.home-subtitle-text {
  max-width: 500px;
  font-size: 2rem;
  color: #373b43;
  text-align: start;
  flex: 1 0;
}

.home-subtitle-text span {
  color: #3e83fb;
}

/* About me */

.home-aboutme-container {
  color: #373b43;
  text-align: center;
  max-width: 450px;
  margin: 30px auto;
}

.home-aboutme-container h2 {
  color: #373b43;
  flex: 3 0;
  font-size: 2rem;
}

.home-aboutme-container p {
  color: #373b43;
  margin: 0 auto;
  text-align: justify;
  font-size: 1.2rem;
}

/* Tools */

.home-tools-container {
  color: #373b43;
  text-align: start;
  font-size: 1rem;
}

.home-tools-container h2 {
  flex: 3 0;
  font-size: 2rem;
  text-align: center;
}

.home-tools-view {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
  margin: 10px auto;
}

/* Bio */
.home-bio-container {
  color: #373b43;
  max-width: 450px;
  margin: 0px auto;

  text-align: center;
}

.home-contact-container {
  color: #373b43;
}

.home-bio-flex-container {
  display: flex;
}

.home-bio-container h2 {
  font-size: 2rem;
}

.home-bio-flex-container h3 {
  color: #3e83fb;
  flex: 2 0;
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  font-size: 1.4rem;
  margin-right: 20px;
}

.home-bio-flex-container p {
  flex: 9 0;
  font-size: 1.2rem;
  line-height: 200%;
  text-align: justify;
}

/* Contact */

.home-contact-container {
  color: #373b43;
  margin: 0px auto;
  text-align: center;
  max-width: 450px;
  width: 100%;
}

.home-contact-container h2 {
  font-size: 2rem;
}

.home-contact-container h3 {
  margin-top: -15px;
}

/* Social Media */
.home-socialmedia-container {
  display: flex;
  width: 100px;
  margin: 20px auto;
  justify-content: space-evenly;
}

.home-socialmedia-container a {
  color: #3e83fb;
}

.home-socialmedia-container a:hover {
}

.home-socialmedia-container a:visited {
  color: #3e83fb;
}

/*Media Querys*/

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  /* Title */
  .home-title-container {
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 150px;
  }

  .home-title-text {
    text-align: center;
    font-size: 6rem;
  }

  .home-subtitle-text {
    margin: auto;
    font-size: 1.7rem;
  }

  /* Tools */
  .home-tools-container {
    margin: 0 auto;
  }

  .home-tools-view {
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 500px;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  /* Title */
  .home-title-text {
    font-size: 8rem;
  }

  .home-subtitle-text {
    font-size: 2rem;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1240px) {
  .home-title-text {
    font-size: 8.5rem;
  }

  .home-subtitle-text {
    font-size: 2.5rem;
  }
}

.mailbutton-container {
  display: flex;
  width: 80%;
  background-color: #3e83fb;
  align-items: center;
  justify-content: space-evenly;
  font-size: 1.1rem;
  border: 0px solid #3e83fb;
  border-radius: 16px;
  padding: 0.9rem 0;
  margin: auto;
}

@media only screen and (min-width: 600px) {
  .mailbutton-container {
    font-size: 1.3rem;
  }
}

.skills-container {
  padding: 0 30px;
  margin: 0 auto;
  max-width: 1100px;
  display: flex;
  flex-direction: column;
  flex-basis: auto;
  align-items: flex-start;
  background-color: #f7f7f7;
}

.skills-title {
  color: #373b43;
  text-align: center;
  font-size: 2.6rem;
  width: 100%;
  margin-bottom: 0px;
}

.projectitem-container {
  display: flex;
  flex-direction: column;
  background-color: #f7f7f7;
  width: 100%;
  height: 525px;
  border-radius: 8px;
  color: #373b43;
}

.projectitem-image {
}

.projectitem-image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  max-height: 200px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.projectitem-text-container {
  padding: 25px 40px;
  flex: 1 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.projectitem-text-title {
  font-size: 1.3rem;
  font-weight: 400;
  margin-bottom: 5px;
}

.projectitem-text-body {
  text-align: justify;
  margin-bottom: 20px;
}

.projectitem-text-link {
  color: #373b43;

  font-size: 0.9rem;
  font-weight: 600;
}

.projects-container {
  padding: 0 30px;
  margin: 0 auto;
  max-width: 1100px;
  flex-direction: column;
  flex-basis: auto;
  align-items: flex-start;
}

.projects-carousel-container {
  margin: 0 auto;
  max-width: 500px;
}

.carousel-container-item {
  padding: 0 auto;
}

.projects-personal-container {
  color: #373b43;
  text-align: center;
  max-width: 450px;
  margin: 30px auto;
  font-size: 1.2rem;
}

.projects-personal-container h2 {
  font-size: 2rem;
  text-align: center;
}

